.success-alert {
    background-color: rgb(0, 214, 0);
    position: fixed;
    font-size: 1.2em;
    width: 200px;
    height: 70px;
    top: 0;
    left: 50%;
    padding: 5px 10px 5px 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    text-align: center;
}
